<template>
    <div class="history-orders">
        <div class="history-orders-title">历史订单</div>
        <div
            v-for="(item, index) in withdrawRecords"
            :key="item.createTime + index"
            class="history-orders-item Hairline Hairline_bottom-outer"
        >
            <p class="order-type">
                <span>{{ item.withdrawType }}</span>
                <span>
                    <span class="money-type">¥</span><span class="money">{{ item.cash }}</span>
                </span>
            </p>
            <p>申请时间：{{ item.applyTime }}</p>
            <p>状态：{{ item.statusDesc }}</p>
            <p
                class="cause-item"
                v-if="item.status === 0 && item.resultDesc"
            >
                <span>原因：</span>
                <span class="order-cause">{{ item.resultDesc }}</span>
            </p>
            <div
                class="button-wrap"
                v-if="item.bingStatus>0">
                <button
                    @click="clickBindButton(item)"
                    class="bind-button"
                    :class="{
                        'bind-idcard': item.bingStatus === 1,
                        'bind-success': item.bingStatus === 2 }">
                    {{ item.bingStatus | buttonText }}
                </button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'WithdrawRecord',
    props: {
        withdrawRecords: {
            type: Array,
            required: true,
        },
    },
    filters: {
        buttonText(value) {
            const map = {
                1: '换绑身份证',
                2: '换绑成功',
            };
            return map[value] || '';
        },
    },
    methods: {
        clickBindButton(item) {
            if (item.bingStatus === 1) {
                this.$router.push('bindId');
            }
        },
    },
};
</script>
