<template>
    <div class="page">
        <nav-bar
            class="header"
            title="提现进度" />
        <div class="scroll-container">
            <!-- v-if 避免 created 渲染子组件；直接利用父组件的 updated 渲染子组件（函数式组件） -->
            <withdraw-progress-detail
                v-if="withdrawRecord"
                :withdraw-record="withdrawRecord" />
            <van-list
                v-if="withdrawRecords.length"
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
            >
                <!-- FIXME: 为了代码整洁，牺牲了性能；model 多遍历一次 -->
                <withdraw-record :withdraw-records="withdrawRecords" />
            </van-list>
            <div
                v-if="nav.imgUrl"
                class="footer"
                @click="goInvite(nav.link)"
            >
                <div
                    :style="{
                        backgroundImage: `url(${nav.imgUrl})`
                    }"
                    class="btn" />
            </div>
        </div>
        <Dialog
            class="dialog"
            :outside-close="false"
            :show.sync="dialog.show"
            :title="dialog.title"
            :msg="dialog.msg"
            :btnText="dialog.btnText"
            :clickHandler="dialog.successHandler"
        >
            <div
                slot="icon"
                class="dialog-icon"
            />
        </Dialog>
    </div>
</template>
<script>
import NavBar from '@/components/NavBar';
import config from '@/config';
import { getConfigByKey } from '@/api/common';
import { getWithdrawRecord, wthdrawRecordModel } from '@/api/withdraw';
import Dialog from '@/components/Dialog';
import {
    isTakeOverBackByWeb,
    openCommonWebViewByPath,
    hasCalendarEventExist,
} from '@/utils/jsApiV2/client';
import { goIndexTab } from '@/utils/jsApiV2/deeplink';
import { addCustomEventListener } from '@/utils/event';
import WithdrawProgressDetail from './WithdrawProgressDetail';
import WithdrawRecord from './WithdrawRecord';
import withdrawMixin from './withdrawMixin';
import { apolloIncomeFooterBanner, defaultNavBanner } from './config';

const calendarEvent = {
    1: {
        title: '【想看】今天可提现1元零钱了，点击提现>>',
        description: '想看1元提现',
        mode: 'FREQ=DAILY;INTERVAL=1;COUNT=1',
    },
    5: {
        title: '【想看】再连续签到3天，可提现5元现金，点击签到>>',
        description: '想看5元提现',
        mode: 'FREQ=DAILY;INTERVAL=1;COUNT=3',
    },
};

export default {
    name: 'WithdrawProgress',
    mixins: [withdrawMixin],
    components: {
        NavBar,
        WithdrawProgressDetail,
        WithdrawRecord,
        Dialog,
    },
    data() {
        return {
            loading: false,
            finished: false,
            withdrawRecord: null,
            withdrawRecords: [],
            nextPage: -1,
            nav: {
                link: '',
                title: '',
                imgUrl: '',
            },
            dialog: {
                show: false,
                title: this.dialogTitle(), // '兑换成功预计10分钟到账',
                msg: `${this.$route.query.withdraw}元兑换成功`,
                btnText: '加入日历提醒，不错过提现',
                successHandler: () => {
                    this.withdrawDialogStats('点击', this.$route.query.cash);
                    this.dialog.show = false;

                    if (this.$route.query.cash === '1' || this.$route.query.cash === '5') {
                        const event = calendarEvent[this.$route.query.cash];
                        const isExit = hasCalendarEventExist(event.title, event.description);

                        if (!isExit) {
                            this.handleAddCalendar(event.title, event.description, event.mode, 1);
                        }
                    }
                },
            },
        };
    },
    created() {
        // 40840开始实验
        if (Number(config.versionCode) > 40830) {
            this.dialog.btnText = '赚更多金币';
            this.dialog.successHandler = () => {
                // 40830新加的tab
                goIndexTab('chatTab');
            };
        }
        this.getNav();
        this.getRecord()
            .then((response) => {
                const model = wthdrawRecordModel(response);

                Object.assign(this, model);
                this.$root.hidePlaceholder();
                const showDialog = () => {
                    const validCash = ['1', '5'].indexOf(this.$route.query.cash) > -1;
                    const validWithdraw = ['0.3', '1'].indexOf(this.$route.query.withdraw) > -1;
                    const versionCode = Number(config.versionCode);
                    const validVersionCode = versionCode && versionCode >= 40770;
                    const show = validCash && validWithdraw && validVersionCode;

                    if (show) {
                        isTakeOverBackByWeb(true);
                        addCustomEventListener('backKey', () => {
                            isTakeOverBackByWeb(false);
                        });
                        this.withdrawDialogStats('曝光', this.$route.query.cash);
                    }

                    return show;
                };

                if (model.nextPage < 1) {
                    this.finished = true;
                    this.loading = false;
                }
                this.$o2o({ e_c: '提现', e_a: '曝光', e_n: '提现进度页面曝光' });
                this.dialog.show = showDialog();
            })
            .catch((error) => {
                console.log(error);
                this.$error();
            });
    },
    methods: {
        withdrawDialogStats(ea, cash) {
            const type = {
                1: '微信1元',
                5: '微信5元',
            };
            this.$o2o({
                e_c: '提现',
                e_a: ea,
                e_n: '日历提醒弹窗曝光',
                e_x: {
                    string_type: type[cash],
                },
            });
        },
        dialogTitle() {
            let title;
            const nextDriblet = this.$route.query.nextDriblet ?
                Number(this.$route.query.nextDriblet)
                : 0;
            if (nextDriblet) {
                title = nextDriblet === 500 ?
                    '<div style="text-align: center;">再连续签到3天，<span style="color: #FF4347;">再提5元！</span></div>'
                    : `<div style="text-align: center;">明日来，<span style="color: #FF4347;">再提${nextDriblet / 100}元！</span></div>`;
            } else {
                title = this.$route.query.withdraw === '0.3'
                    ? '<div style="text-align: center;">明日来，<span style="color: #FF4347;">再提1元！</span></div>'
                    : '<div style="text-align: center;">再连续签到3天，<span style="color: #FF4347;">再提5元！</span></div>';
            }
            return title;
        },
        getRecord(page = 1) {
            return getWithdrawRecord({ page });
        },
        goInvite(link) {
            if (link) {
                openCommonWebViewByPath(`${link}`);
                this.$o2o({
                    e_c: '提现',
                    e_a: '点击',
                    e_n: '提现进度底部按钮',
                    e_x: JSON.stringify({
                        name: this.nav.title,
                    }) });
            }
        },
        onLoad() {
            if (this.nextPage > 1) {
                this.loading = true;
                this.getRecord(this.nextPage)
                    .then((response) => {
                        const model = wthdrawRecordModel(response);

                        if (model.nextPage < 1) {
                            this.finished = true;
                        }

                        this.nextPage = model.nextPage;
                        if (model.withdrawRecords) {
                            this.withdrawRecords =
                                this.withdrawRecords.concat(model.withdrawRecords);
                        }
                        this.loading = false;
                    })
                    .catch(() => {
                        this.loading = false;
                        this.finished = true;
                    });
            } else {
                this.finished = true;
            }
        },
        getNav() {
            getConfigByKey({
                key: apolloIncomeFooterBanner,
            })
                .then((res) => {
                    const footerBannerConfig = res.data.withdrawProgress;
                    const randomIndex = parseInt(Math.random() * footerBannerConfig.length, 10);
                    this.nav = footerBannerConfig[randomIndex];
                    this.$o2o({
                        e_c: '提现',
                        e_a: '曝光',
                        e_n: '提现进度底部按钮',
                        e_x: JSON.stringify({
                            name: this.nav.title,
                        }) });
                })
                .catch(() => {
                    // 当阿波罗配置报错或者超时 给定默认值
                    this.nav = defaultNavBanner;
                    this.$o2o({
                        e_c: '提现',
                        e_a: '曝光',
                        e_n: '提现进度底部按钮',
                        e_x: JSON.stringify({
                            name: this.nav.title,
                        }) });
                });
        },
    },
};
</script>
