    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 73px;
    background-color: #fff;
    box-shadow: 0 0 23px 0 rgba(0, 0, 0, 0.1);
}

.btn {
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
    background-repeat: no-repeat;
}

.dialog >>> .dialog__content {
    width: 270px;
    background: linear-gradient(180deg, rgba(255, 247, 226, 1) 0%, rgba(255, 255, 255, 1) 50%);
}

.dialog >>> .button {
    width: 220px !important;
    height: 43px !important;
    background: linear-gradient(180deg, rgba(255, 137, 48, 1) 0%, rgba(255, 50, 36, 1) 100%);
    box-shadow: 0 5px 8px 0 rgba(255, 72, 38, 0.22);
    font-size: 15px !important;
    line-height: 20px;
    margin: 37px auto 26px;
    font-weight: 600;
}

.dialog >>> .dialog__inner {
    padding-top: 28px;
    position: relative;
}

.dialog >>> .dialog__msg h1 {
    font-weight: 600;
    font-size: 22px;
