    margin-top: 20px;
}

.dialog >>> .dialog__close {
    background-size: 14px 14px;
    margin-top: 15px;
}

.dialog >>> .dialog-icon {
    height: 91px;
    width: 106px;
    position: absolute;
    left: 50%;
